import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import style from './newsListDesk.module.css';

const length = someString => {
  if (someString.length <= 37) return someString;
  return `${someString.slice(0, 38)}...`;
};

const largeLength = someString => {
  if (someString.length <= 200) return someString;
  return `${someString.slice(0, 201)}...`;
};

const NewsListDesk = ({ news = [] }) => {
  return (
    <Fragment>
      {news.length > 0 && (
        <ul className={style.list}>
          {news.map(item => (
            <li className={style.listItem} key={item.id}>
              <picture>
                <source srcSet={item.imgW} type="image/webp" />
                <source srcSet={item.imgJ} type="image/jp2" />
                <source srcSet={item.img} type="image/jpg" />
                <img className={style.imgBack} src={item.img} alt="news img" />
              </picture>
              <div className={style.date}>{item.date}</div>
              <div className={style.newsBlock}>
                <p className={style.newsHead}>{length(item.title)}</p>
                <p className={style.content}>
                  {largeLength(item.shortContent)}
                </p>
                <div
                  className={style.link}
                  to={`/news/singleArticle/${item.id}`}
                >
                  <i className="material-icons">keyboard_backspace</i>
                </div>
              </div>
              <Link
                className={style.linkLink}
                to={`/news/singleArticle/${item.id}`}
              />
            </li>
          ))}
        </ul>
      )}
      {!news.length && (
        <h2 className={style.error}>Not found news by this category</h2>
      )}
    </Fragment>
  );
};

export default NewsListDesk;
