import React, { Component, Fragment } from 'react';
import { Switch, Route } from 'react-router-dom';
import Localization from '../../context/localization';
import FirstScreen from '../../components/first100vh/first100vh';
import MapControl from '../../components/mapControl/mapControl';
import styles from './startPage.module.css';
import NewsListMain from '../../components/newsIndex/newsIndex';
import NewsCategoriesPage from '../newsCategoriesPage/newsCategoriesPage';
import Footer from '../../components/footer/footer';
import SinglePageArticle from '../singleArticle/singleArticle';
import InProgress from '../errorPage/errorPage';

class StartPage extends Component {
  state = {};

  render() {
    const {
      autorization,
      header,
      currentLocale,
      officeNavigation,
      quickMenu,
      map,
      footer,
      newsMain,
      newsContent,
      singleNews,
    } = this.props;
    return (
      <Fragment>
        <FirstScreen
          autorization={autorization}
          header={header}
          currentLocale={currentLocale}
          officeNavigation={officeNavigation}
          quickMenu={quickMenu}
        />
        <div className={styles.contentBlock}>
          <Switch>
            <Route
              exact
              path="/"
              render={() => (
                <Fragment>
                  <MapControl {...map} currentLocale={currentLocale} />
                  <NewsListMain currentLocale={currentLocale} {...newsMain} />
                </Fragment>
              )}
            />
            <Route
              path="/enterprise"
              render={() => (
                <Fragment>
                  <MapControl {...map} currentLocale={currentLocale} />
                  <NewsListMain currentLocale={currentLocale} {...newsMain} />
                </Fragment>
              )}
            />
            <Route
              path="/press"
              render={() => (
                <Fragment>
                  <MapControl {...map} currentLocale={currentLocale} />
                  <NewsListMain currentLocale={currentLocale} {...newsMain} />
                </Fragment>
              )}
            />
            <Route
              path="/news/singleArticle/:id"
              render={() => (
                <SinglePageArticle
                  currentLocale={currentLocale}
                  {...singleNews}
                />
              )}
            />
            <Route
              path="/news"
              render={() => (
                <NewsCategoriesPage
                  currentLocale={currentLocale}
                  newsContent={newsContent}
                />
              )}
            />
            <Route render={() => <InProgress />} />
          </Switch>
        </div>
        <Footer currentLocale={currentLocale} footer={footer} />
      </Fragment>
    );
  }
}

export default function LocalizedStartPage() {
  return (
    <Localization.Consumer>
      {({ localizedContent, currentLocale }) => (
        <StartPage {...localizedContent} currentLocale={currentLocale} />
      )}
    </Localization.Consumer>
  );
}
