import React, { Component, Fragment } from 'react';
import Media from 'react-media';
import style from './buildingNewsList.module.css';
import NewsListDesk from '../../components/newsList/newsListDesk/newsListDesk';
import NewsListMob from '../../components/newsList/newsListMob/newsListMob';
import newsList from '../../bd/newsContent.json';

const qsTrue = qs => {
  if (qs === '/news' || qs === '/news/') {
    return true;
  }
  return false;
};

const articlesToRender = (articles, qs, currentLocale) => {
  const articlesLang =
    currentLocale === 'uk' ? articles.newsUk : articles.newsRu;
  const filterdedArticles = qsTrue(qs)
    ? articlesLang
    : articlesLang.filter(el => el.category === qs.slice(6));
  return filterdedArticles;
};

const articlesToRenderDate = (articles, qs, currentLocale) => {
  const year = qs.slice(13, 17);
  const month = qs.slice(-2);
  const articlesLang =
    currentLocale === 'uk' ? articles.newsUk : articles.newsRu;
  const filterdedArticles = articlesLang.filter(el =>
    el.date.includes(`${month}/${year}`),
  );
  return filterdedArticles;
};

export default class BuildingNewsList extends Component {
  state = {
    articles: {},
  };

  componentDidMount = () => {
    window.scrollTo(0, 120);
    this.setState({ articles: newsList });
  };

  render() {
    const { articles } = this.state;
    const { qs, currentLocale, button } = this.props;
    const articlesRender =
      currentLocale === 'uk' ? articles.newsUk : articles.newsRu;
    const category = qs.includes('search');

    return (
      <Fragment>
        {articlesRender && articlesRender.length && (
          <Media query={{ minWidth: 1200 }}>
            {matches =>
              matches ? (
                <NewsListDesk
                  news={
                    !category
                      ? articlesToRender(articles, qs, currentLocale)
                      : articlesToRenderDate(articles, qs, currentLocale)
                  }
                />
              ) : (
                <NewsListMob
                  news={
                    !category
                      ? articlesToRender(articles, qs, currentLocale)
                      : articlesToRenderDate(articles, qs, currentLocale)
                  }
                />
              )
            }
          </Media>
        )}
        {articlesRender &&
          articlesRender.length &&
          !category &&
          articlesToRender(articles, qs, currentLocale).length > 11 && (
            <button className={style.showMore}>{button}</button>
          )}
      </Fragment>
    );
  }
}
