import React from 'react';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import arrow from '../../../assets/img/icons/arrow-right-full.svg';
import style from './dateFinder.module.css';

const setMonth = month => `0${month + 1}`;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexWrap: 'no-wrap',
    alignItems: 'center',
    width: 255,
    height: 40,
    border: '1px solid #60B6D9',
    borderRadius: '3px',
    '& .MuiInput-underline:before': {
      content: 'none',
    },
    '& .MuiInput-underline:after': {
      borderBottom: 'none',
    },
    '& .MuiSelect-icon': {
      fill: '#ffffff',
    },
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    '&:nth-child(2)': {
      minWidth: 60,
    },
  },
  selectEmpty: {
    marginTop: 0,
    textTransform: 'uppercase',
    color: '#ffffff',
  },
}));

export default function SimpleSelect(props) {
  const classes = useStyles();
  const [values, setValues] = React.useState({
    month: '0',
    year: '2019',
  });

  function handleChange(event) {
    setValues(oldValues => ({
      ...oldValues,
      [event.target.name]: event.target.value,
    }));
  }
  const { monthes = [], years = [], month, year } = props;

  return (
    <form className={classes.root} autoComplete="off">
      <FormControl className={classes.formControl}>
        <Select
          value={values.month}
          onChange={handleChange}
          name="month"
          className={classes.selectEmpty}
        >
          <MenuItem value="" disabled>
            {month}
          </MenuItem>
          {monthes.map((el, i) => (
            <MenuItem value={i} key={el}>
              {el}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <FormControl className={classes.formControl}>
        <Select
          value={values.year}
          onChange={handleChange}
          name="year"
          displayEmpty
          className={classes.selectEmpty}
        >
          <MenuItem value="" disabled>
            {year}
          </MenuItem>
          {years.map(el => (
            <MenuItem value={el} key={el}>
              {el}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <Link
        className={style.linkArrow}
        to={`/news/search:${values.year}month${
          values.month > 8 ? values.month + 1 : setMonth(values.month)
        }`}
      >
        <img src={arrow} alt="arrow" className={style.arrow} />
      </Link>
    </form>
  );
}
