import React from 'react';
import Media from 'react-media';
import { NavLink } from 'react-router-dom';
import DateFinder from './dateFinder/dateFinder';
import DateFinderMob from './dateFinderMob/dateFinderMob';
import NewsNavMob from './newsNavMob/newsNavMob';
import style from './newsNav.module.css';

const NewsNav = ({ newsNav = [], header, calendar }) => {
  return (
    <div className={style.navWrapper}>
      <Media query={{ minWidth: 1200 }}>
        {matches =>
          matches ? (
            <ul className={style.linkList}>
              <li className={style.title}>{header}</li>
              {newsNav.map(el => (
                <li className={style.linkItem} key={el.id}>
                  <NavLink
                    exact
                    activeClassName={style.selected}
                    className={style.link}
                    to={el.link}
                  >
                    {el.name}
                  </NavLink>
                </li>
              ))}
            </ul>
          ) : (
            <div className={style.order}>
              <NewsNavMob newsNav={newsNav} />
            </div>
          )
        }
      </Media>
      <div className={style.calendar}>
        <Media query={{ minWidth: 1200 }}>
          {matches =>
            matches ? (
              <DateFinder {...calendar} />
            ) : (
              <div className={style.mobCalendar}>
                <h3 className={style.titleMob}>{header}</h3>
                <DateFinderMob {...calendar} />
              </div>
            )
          }
        </Media>
      </div>
    </div>
  );
};

export default NewsNav;
